import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Search from '@/components/Search'
import SearchMore from '@/components/Search-more'
import Subject from '@/components/Subject'
import Designer from '@/components/Designer'
import GoodsDetail from '@/components/Goods-detail'
import DesignerDetail from '@/components/Designer-detail'

import Demo from '@/components/Demo'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			name: 'Home',
			component: Home,
		},
		{
			path: '/Search',
			name: 'Search',
			component: Search,
		},
		{
			path: '/SearchMore/type/:type',
			name: 'SearchMore',
			component: SearchMore,
		},
		{
			path: '/Subject/sid/:sid',
			name: 'Subject',
			component: Subject,
		},
		{
			path: '/Designer',
			name: 'Designer',
			component: Designer,
		},
		{
			path: '/GoodsDetail/gid/:gid',
			name: 'GoodsDetail',
			component: GoodsDetail,
		},
		{
			path: '/DesignerDetail/did/:did',
			name: 'DesignerDetail',
			component: DesignerDetail,
		},
		{
			path: '/Demo',
			name: 'Demo',
			component: Demo,
		}
	]
})



